export default {
    data() {
        return {
            is_show: false,
            latitude: 39.909,
            longitude: 116.39742,
            // 顺序是 配送员位置 用户位置 商家位置
            covers: [{
                latitude: 0,
                longitude: 0,
                iconPath: '',
                width: 50,
                height: 50
            }, {
                latitude: 0,
                longitude: 0,
                iconPath: 'https://yewi.vvv6g.cn/web/mini_images/icon/city-service/my.png',
                width: 50,
                height: 50
            },
            {
                latitude: 0,
                longitude: 0,
                iconPath: 'https://yewi.vvv6g.cn/web/mini_images/icon/city-service/shop.png',
                width: 50,
                height: 50
            }],
            express_id: 0,
            expressData: {}
        }
    },
    methods: {
        getExpressInfo() {
            this.$showLoading();
            this.$request({
                url: this.$api.order.city_map,
                data: {
                    express_id: this.express_id,
                },
                method: 'POST',
            }).then(response => {
                this.$hideLoading();
                this.is_show = true;
                if (response.code === 0) {
                    this.expressData = response.data.express_data;
                    let {
                        user_longitude, user_latitude,
                        man_longitude, man_latitude,
                        corporation_icon, shop_longitude,
                        shop_latitude
                    } = this.expressData;
                    this.longitude = user_longitude;
                    this.latitude = user_latitude;
                    this.covers[0].longitude = man_longitude;
                    this.covers[0].latitude = man_latitude;
                    this.covers[0].iconPath = corporation_icon;

                    this.covers[1].longitude = user_longitude;
                    this.covers[1].latitude = user_latitude;

                    this.covers[2].longitude = shop_longitude;
                    this.covers[2].latitude = shop_latitude;
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.express_id = options.express_id;
        this.getExpressInfo();
    }
}
